import React, { FC } from 'react'
import styled from 'styled-components'
import { RichTextType } from 'types/dataTypes'
import { Title, Text } from './'

const StyledRichText = styled.div`
  h1,
  h2,
  h3,
  h4,
  h5 {
    color: ${({ theme }) => theme.colors.gray[900]};
    margin-bottom: 1rem;
  }

  p {
    color: ${({ theme }) => theme.colors.gray[500]};
    margin-bottom: 1.5rem;
  }

  img {
    width: 100%;
    height: auto;
    margin: 3rem 0;
    display: block;
  }
`

type RichTextParams = {
  content: RichTextType
}

const RichText: FC<RichTextParams> = ({ content }) => {
  return (
    <StyledRichText>
      {content.richText.map((item, idx) => {
        switch (item.type) {
          case 'heading1':
            return <h1 key={idx}>{item.text}</h1>
          case 'heading2':
            return (
              <Title key={idx} as="h2" size="xs">
                {item.text}
              </Title>
            )
          case 'heading3':
            return (
              <Text key={idx} as="h3" size="xl">
                {item.text}
              </Text>
            )
          case 'heading4':
            return (
              <Text key={idx} as="h4" size="xl">
                {item.text}
              </Text>
            )
          case 'paragraph':
            return (
              <Text key={idx} size="lg">
                {item.text}
              </Text>
            )
          case 'image':
            return <img key={idx} src={item.url} alt={item.alt} />
        }
      })}
    </StyledRichText>
  )
}
export default RichText
