import React, { FC } from 'react'
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api'

type MapParams = {
  height?: string
  zoom?: number
  position?: { lat: number; lng: number }
  defaultMarker?: boolean
  markers?: { lat: number; lng: number }[]
}

const Map: FC<MapParams> = ({
  height,
  zoom,
  position,
  defaultMarker,
  markers,
}) => {
  const icon =
    'https://res.cloudinary.com/dzigftse0/image/upload/v1648723333/marker_pkaraa.svg'
  const defaultProps = {
    center: {
      lat: position?.lat || 48.80767,
      lng: position?.lng || 2.2403,
    },
    zoom: zoom || 5.75,
  }

  return (
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    <LoadScript googleMapsApiKey={process.env.GOOGLE_MAPS_API_KEY || ''}>
      <GoogleMap
        mapContainerStyle={{ height: height || '528px' }}
        center={defaultProps.center}
        zoom={defaultProps.zoom}
      >
        {defaultMarker && (
          <Marker
            icon={icon}
            position={{
              lat: defaultProps.center.lat,
              lng: defaultProps.center.lng,
            }}
          />
        )}
        {markers &&
          markers.map((marker, idx) => (
            <Marker
              key={idx}
              icon={icon}
              position={{
                lat: marker.lat,
                lng: marker.lng,
              }}
            />
          ))}
      </GoogleMap>
    </LoadScript>
  )
}

export default Map
